import React from "react";
import { Carousel } from "antd";

function ImageSlider(props) {
  // console.log(props.images);
  return (
    <div>
      <Carousel autoplay>
        {props.images.map((image, index) => (
          <div key={index}>
            <img
              style={{ width: "100%", maxHeight: "150px" }}
              src={`${image}`}
              alt="reviewImage"
            />
          </div>
        ))}
      </Carousel>
    </div>
  );
}

export default ImageSlider;
